'use client'
import { useState, useContext } from 'react'
import { BalanceContext } from '../context/BalanceContext'
import Spinner from './Spinner'

const GameBoard = () => {
    const { balance, updateBalance } = useContext(BalanceContext)
    const [bet, setBet] = useState<number>(10)
    const [reels, setReels] = useState<string[][]>([])
    const [result, setResult] = useState<string | null>(null)
    const [isSpinning, setIsSpinning] = useState(false) // Disable spin button while spinning

    const columns = 3 // Number of columns (reels)
    const rows = 5 // Number of rows

    const spinReels = async () => {
        if (bet > balance) {
            setResult('Insufficient balance!')
            return
        }

        setIsSpinning(true) // Disable spin button
        setResult(null) // Clear result

        // Simulate spinning animation before fetching the result
        setReels(
            Array.from(
                { length: columns },
                () => Array.from({ length: rows }, () => 'ðŸ’«') // Temporary spinning symbol
            )
        )

        // Add a timeout to simulate spinning duration
        setTimeout(async () => {
            const response = await fetch(
                'http://localhost:3001/api/game/spin',
                {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ bet, balance }),
                }
            )

            const data = await response.json()
            setReels(data.reels) // Update with actual results
            updateBalance(data.newBalance)
            setResult(data.isWin ? `You win $${data.payout}!` : 'You lose!')
            setIsSpinning(false) // Re-enable spin button
        }, 2000) // Adjust spinning duration (2 seconds)
    }
    console.log(reels)
    return (
        <div className="text-center">
            {/* Slot Machine */}
            <div className="flex justify-center space-x-4 mb-4">
                {reels.map((reel, i) => (
                    <div key={i} className="flex flex-row">
                        {reel.map((symbol, j) => (
                            <div
                                key={j}
                                className="w-16 h-16 flex items-center justify-center text-2xl bg-gray-200 rounded-md border"
                            >
                                <div className="symbol">{symbol}</div>
                            </div>
                        ))}
                        {/* <div className="reel-container">
                            {reel.map((symbol, j) => (
                                <div
                                    key={j}
                                    className="reel w-16 h-16 flex items-center justify-center text-2xl bg-gray-200 rounded-md border"
                                >
                                    <div className="symbol">{symbol}</div>
                                </div>
                            ))}
                        </div> */}
                    </div>
                ))}
            </div>

            {/* Bet Input */}
            <div className="mb-4">
                <input
                    type="number"
                    value={bet}
                    onChange={(e) => setBet(Number(e.target.value))}
                    min="1"
                    max={balance}
                    className="border rounded-md px-2 py-1"
                />
                <span> Bet</span>
            </div>

            {/* Spin Button */}
            <button
                onClick={spinReels}
                disabled={isSpinning} // Disable while spinning
                className={`px-4 py-2 rounded-md ${
                    isSpinning
                        ? 'bg-gray-400 text-gray-700'
                        : 'bg-green-500 text-white'
                }`}
            >
                {isSpinning ? <Spinner /> : 'Spin'}
            </button>

            {/* Result and Balance */}
            {result && <p className="mt-4 text-lg">{result}</p>}
            <p className="mt-2">Balance: ${balance}</p>
        </div>
    )
}

export default GameBoard
